import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Swiper from 'swiper/dist/js/swiper.js';
import MyHeader from './MyHeader';
import MyFooter from './MyFooter';
import MyNav from './MyNav';
import 'swiper/dist/css/swiper.min.css';
import '../style/food.css';

class Food extends Component {
  constructor(props){
    super(props);
  }

  state ={
    collapsed:false,
  };

  componentDidMount(){
    new Swiper('.swiper-container',{
      spaceBetween:30,
      centeredSlides:true,
      autoplay:{
        delay:3000,
        disableOnInteraction:false,
      },
      pagination:{
        el:'.swiper-pagination',
        clickable: true,
      },
      navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
      },
      effect: 'fade',
    });
  };

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  }

  render(){
    return(
      <div className="wtf">
        <MyHeader />
        <MyNav />
        <div className="swiper-container">
          <div className="swiper-wrapper">
            <div className="swiper-slide">
              <img src={require('../img/zhuJiao.jpg')} />
              <div className="wordWrapper">
                <div className="wordHead"><h1></h1></div>
                <div className="wordDetail"></div>
              </div>
            </div>
            <div className="swiper-slide">
              <img src={require('../img/douJi.png')} />
              <div className="wordWrapper">
                <div className="wordHead"><h1></h1></div>
                <div className="wordDetail"></div>
              </div>
            </div>
            <div className="swiper-slide">
              <img src={require('../img/yuWan.png')} />
              <div className="wordWrapper">
                <div className="wordHead"><h1></h1></div>
                <div className="wordDetail"></div>
              </div>
            </div>
            <div className="swiper-slide">
              <img src={require('../img/baoYu.png')} />
              <div className="wordWrapper">
                <div className="wordHead"><h1></h1></div>
                <div className="wordDetail">23</div>
              </div>
            </div>
            <div className="swiper-slide">
              <img src={require('../img/liZhi.jpg')} />
              <div className="wordWrapper">
                <div className="wordHead"><h1></h1></div>
                <div className="wordDetail">87%90%</div>
              </div>
            </div>
          </div>
          <div className="swiper-pagination"></div>
          <div className="swiper-button-next"></div>
          <div className="swiper-button-prev"></div>
        </div>
        <MyFooter />
      </div>
    );
  }


}

export default Food;



